@charset "UTF-8";
/* Colores */
/* Fuente */
/* Remplazo de futura */
/* Tamaño de Media Queries */
html {
  /* 1rem = 10px */
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
}

a {
  text-decoration: none;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  .nombre-pagina {
    font-size: 3rem;
  }
}

.nombre-seccion {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .nombre-seccion {
    font-size: 2.4rem;
    text-align: justify;
  }
}

.descripcion-pagina {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 2rem;
}

.enlace-pagina {
  color: #000000;
  text-align: center;
  font-size: 2rem;
}
.enlace-pagina:hover {
  color: #000000;
  text-decoration: underline;
}

.contenidoFlex {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenidoFlex img {
  margin-bottom: 2rem;
  width: 15rem;
}
@media (min-width: 768px) {
  .contenidoFlex img {
    width: 25rem;
  }
}

.hrTexto {
  width: 90%;
  height: 2.3rem;
  border-bottom: 1px solid #000000;
  text-align: center;
}
@media (min-width: 768px) {
  .hrTexto {
    width: 100%;
  }
}
.hrTexto span {
  font-size: 3rem;
  padding: 0 1rem;
  background-color: #ffffff;
}

.logoRojo {
  color: #e93b45;
}

.text-justify {
  text-align: justify;
}

.swiper {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

.swiper-grid-column > .swiper-wrapper {
  flex-direction: row !important;
}

.swiper-slide .swiper-slide-next {
  margin-top: 0 !important;
  margin-right: 0 !important;
}

.swiper-slide img {
  width: 100%;
  height: 15rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .swiper-slide img {
    height: 20rem;
  }
}
@media (min-width: 1024px) {
  .swiper-slide img {
    height: 30rem;
  }
}

.my-10 {
  margin-top: 10rem;
  margin-bottom: 10rem;
}

.carrusel-foto {
  display: block;
  width: 100% !important;
  height: 30rem;
}
@media (min-width: 768px) {
  .carrusel-foto {
    height: 60rem;
  }
}

.formulario {
  width: 100%;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .formulario {
    width: 50%;
  }
}
.formulario span {
  color: #e93b45;
}
.formulario img {
  width: 25rem;
}

.campo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .campo {
    flex-direction: row;
    margin-top: 2rem;
  }
}
.campo label {
  display: block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .campo label {
    margin-top: 0;
    flex: 0 0 11rem;
  }
}
.campo input,
.campo select {
  border: 1px solid #000000;
  width: 90%;
  margin: 1rem 0 0;
  padding: 0.5rem 2rem;
  border-radius: 1rem;
  display: block;
}
@media (min-width: 768px) {
  .campo input,
  .campo select {
    flex: 1;
    margin: 0;
  }
}
.campo a {
  margin-top: 2rem;
  color: #000000;
}
.campo a:hover {
  text-decoration: underline;
}

.formulario-buscador {
  font-size: 1.4rem;
  display: flex;
  gap: 1rem;
}
.formulario-buscador input:not([type=submit]) {
  border: 1px solid #000000;
  width: 100%;
  margin: 1rem 0 0;
  padding: 0.5rem 2rem;
  border-radius: 1rem;
  display: block;
}
@media (min-width: 768px) {
  .formulario-buscador input:not([type=submit]) {
    flex: 1;
    margin: 0;
  }
}
.formulario-buscador .boton {
  padding: 0;
  margin: 1rem 0 0;
}
@media (min-width: 768px) {
  .formulario-buscador .boton {
    margin: 0;
    padding: 1rem 4rem;
  }
}

.alerta {
  padding: 1rem;
  color: #ffffff;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1.8rem;
}
.alerta.error {
  background-color: #e93b45;
}
.alerta.exito {
  background-color: #009B57;
}

.boton {
  background-color: #e93b45;
  color: #000000;
  border: none !important;
  border-radius: 0.5rem;
  width: 90%;
  margin: 3rem 0;
  padding: 1rem 4rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
    margin: 0;
  }
}
.boton:hover {
  background-color: #d91823;
  cursor: pointer;
  color: #000000;
}

.boton-Pendiente {
  background-color: #008AFF;
  color: #000000;
  border: none !important;
  border-radius: 0.5rem;
  width: 90%;
  margin: 3rem 0;
  padding: 1rem 4rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 0;
}
@media (min-width: 768px) {
  .boton-Pendiente {
    width: auto;
    margin: 0;
  }
}
.boton-Pendiente:hover {
  background-color: #006ecc;
  cursor: pointer;
  color: #000000;
}

.boton-Terminado {
  background-color: #009B57;
  color: #000000;
  border: none !important;
  border-radius: 0.5rem;
  width: 90%;
  margin: 3rem 0;
  padding: 1rem 4rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 0;
}
@media (min-width: 768px) {
  .boton-Terminado {
    width: auto;
    margin: 0;
  }
}
.boton-Terminado:hover {
  background-color: #00683a;
  cursor: pointer;
  color: #000000;
}

.botonLink {
  color: #000000;
  background-color: #e93b45;
  text-transform: uppercase;
  font-size: 1.4rem;
  padding: 0.5rem 1rem;
  margin: 0 2rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .botonLink {
    padding: 1rem;
    font-size: 1.8rem;
  }
}

.whatsapp {
  position: fixed;
  bottom: 0;
  right: 0;
  margin-bottom: 5rem;
  margin-right: 5rem;
  height: 3rem;
  width: 3rem;
  z-index: 1;
}
.whatsapp-form {
  width: 20rem;
}
@media (min-width: 768px) {
  .whatsapp-form {
    width: 25rem;
  }
}
@media (min-width: 1024px) {
  .whatsapp-form {
    width: 30rem;
  }
}

.home {
  background-color: #252525;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.home .left-mobile img {
  margin: 1rem 2rem;
  width: 5rem;
}
.home .left-mobile img:last-child {
  margin: 1rem;
  width: 3rem;
}
.home .left-desktop {
  display: none;
}
.home .left-desktop img {
  margin: 1rem 2rem;
  width: 5rem;
}
.home .left-desktop .opcionesHeader {
  color: #ffffff;
  font-size: 1.8rem;
  padding: 1rem;
  text-transform: uppercase;
}
.home .left-desktop .opcionesHeader:hover {
  text-decoration: underline;
}
@media (min-width: 1024px) {
  .home .left-mobile {
    display: none;
  }
  .home .left-desktop {
    display: block;
  }
}
@media (min-width: 768px) {
  .home .right {
    margin-bottom: 0.7rem;
  }
}
.home .right a:hover {
  color: #000000 !important;
}

.tituloAdmin {
  color: #ffffff;
  background-color: #252525;
  text-transform: uppercase;
  font-size: 1.4rem;
  text-align: center;
  margin: 0;
  padding: 1rem 0 0 0;
}
@media (min-width: 768px) {
  .tituloAdmin {
    font-size: 1.8rem;
    letter-spacing: 0.5rem;
  }
}

/* Bootstrap Offcanvas */
#offcanvasHome,
#offcanvasAdmin {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
}

.offcanvas-body a {
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  margin: 3rem 0;
}
.offcanvas-body a:hover {
  letter-spacing: 0.5rem;
  transition-duration: 0.3s;
}

.offcanvas-header {
  display: flex;
  align-items: center;
}
.offcanvas-header p {
  color: #ffffff;
  font-weight: bold;
  font-size: 3rem;
  margin: 0;
}
.offcanvas-header button {
  background-color: #e93b45;
  font-size: 3rem;
}

.dropdown-menu li a {
  color: #000000;
  font-size: 1.4rem;
  padding: 1rem;
}
.dropdown-menu li a:hover {
  text-decoration: underline;
}
@media (min-width: 768px) {
  .dropdown-menu li a {
    font-size: 1.8rem;
  }
}

.footer {
  background-color: #252525;
  color: #ffffff;
  padding: 3rem;
  margin-top: 20rem;
}
.footer .politica-privacidad {
  color: #ffffff;
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: 1.4rem;
  font-style: italic;
  margin-top: 5rem;
}
.footer .footerGrid h3 {
  font-weight: bold;
  color: #e93b45;
  font-size: 1.6rem;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .footer .footerGrid h3 {
    font-size: 2rem;
    margin-top: 0;
  }
}
.footer .footerGrid .footerTitulo {
  margin-top: 0rem !important;
}
.footer .footerGrid p,
.footer .footerGrid a {
  color: #ffffff;
  display: block;
  font-size: 1.4rem;
  margin: 0;
}
@media (min-width: 768px) {
  .footer .footerGrid p,
  .footer .footerGrid a {
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .footer .footerGrid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .footer .footerGrid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.error {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
.error .errorTitulo {
  margin-top: 5rem;
  text-align: center;
  color: #e93b45;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .error .errorTitulo {
    font-size: 4rem;
  }
}
.error .errorImagen {
  width: 90%;
}
@media (min-width: 768px) {
  .error .errorImagen {
    width: 60%;
  }
}

.mensaje {
  display: flex;
  justify-content: center;
}
.mensaje img {
  margin: 3rem 0;
  width: 75%;
}
@media (min-width: 768px) {
  .mensaje img {
    width: 50%;
  }
}

.pedidos {
  list-style: none;
  padding: 0;
  margin-top: 5rem;
}
.pedidos h3 {
  font-weight: bold;
  text-align: center;
  font-size: 1.6rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .pedidos h3 {
    font-size: 2rem;
  }
}
.pedidos p {
  font-weight: 700;
  color: #e93b45;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .pedidos p {
    font-size: 1.8rem;
  }
}
@media (min-width: 768px) {
  .pedidos .datosClientes {
    display: grid;
    gap: 0.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .pedidos .datosClientes {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.pedidos span,
.pedidos .servicio {
  font-weight: 400;
  color: #000000;
}
.pedidos .total {
  font-weight: 700;
  color: #e93b45;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .pedidos .total {
    font-size: 1.8rem;
  }
}
.pedidos .total span {
  font-weight: 400;
  color: #000000;
}

.inventario-header {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
@media (min-width: 768px) {
  .inventario-header {
    flex-direction: row;
  }
}
.inventario-header .boton-Pendiente {
  color: #000000;
  font-size: 1.4rem;
  width: 100%;
}

.gridCards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .gridCards {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .gridCards {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.gridCards button {
  font-size: calc(1.3rem + 0.6vw);
}

.carta-width {
  width: 90%;
}

.carta-height {
  height: 20rem;
}

.contendorPlatillo {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  text-align: center;
  gap: 3rem;
}
.contendorPlatillo .tituloMobile {
  font-size: 2.6rem;
  margin-bottom: 3rem;
}
.contendorPlatillo .tituloDesktop {
  display: none;
}
.contendorPlatillo img {
  width: 90%;
  max-height: 50rem;
}
.contendorPlatillo h1 {
  font-weight: bold;
}
.contendorPlatillo p {
  font-size: 1.8rem;
}
.contendorPlatillo p span {
  color: #e93b45;
}
@media (min-width: 768px) {
  .contendorPlatillo {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    text-align: left;
  }
  .contendorPlatillo .tituloMobile {
    display: none;
  }
  .contendorPlatillo .tituloDesktop {
    display: block;
  }
}

.testimonio {
  background-color: #e93b45;
  padding: 3rem;
  height: 70rem;
}
@media (min-width: 768px) {
  .testimonio {
    padding: 6rem;
    height: 40rem;
  }
}
@media (min-width: 1024px) {
  .testimonio {
    padding: 10rem;
  }
}
.testimonio-footer {
  display: flex;
  justify-content: flex-end;
  margin-top: 3rem;
}
.testimonio-img {
  width: 5rem;
}
@media (min-width: 768px) {
  .testimonio-img {
    width: 10rem;
  }
}
.testimonio-h3 {
  font-size: 2rem;
  color: #ffffff;
  font-weight: bold;
  font-style: italic;
}
.testimonio-body {
  font-size: 1.8rem;
  color: #ffffff;
  text-align: justify;
  line-height: 2;
}

.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .dashboard__mobile {
    display: none;
  }
}
.dashboard__tablet {
  display: none;
}
@media (min-width: 768px) {
  .dashboard__tablet {
    display: block;
  }
}
.dashboard__logo {
  color: #ffffff;
  margin: 0;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto; /* para que se quede estatico mientras el contenido avanza */
}
.dashboard__sidebar {
  display: none;
}
@media (min-width: 768px) {
  .dashboard__sidebar {
    display: block;
    background-color: #9a9a9a;
    width: 15rem;
    min-height: 100vh;
  }
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.dashboard__contenido {
  width: 100%;
  padding: 1rem;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 3rem 3rem 0 3rem;
  }
}

.dashboard-total {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .dashboard-total {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .dashboard-total {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.dashboard-total__content {
  display: grid;
  grid-template-columns: 40% 60%;
}
.dashboard-total__left, .dashboard-total__left--amarrillo, .dashboard-total__left--azul, .dashboard-total__left--rojo, .dashboard-total__left--verde {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dashboard-total__left--verde {
  background-color: #009B57;
}
.dashboard-total__left--rojo {
  background-color: #e93b45;
}
.dashboard-total__left--azul {
  background-color: #008AFF;
}
.dashboard-total__left--amarrillo {
  background-color: #7D63FF;
}
.dashboard-total__icon {
  font-size: 5rem;
  color: #ffffff;
}
.dashboard-total__right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.dashboard-table {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .dashboard-table {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .dashboard-table {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}